<template>
    <div class="self-introduction-box">
        <div class="self-myinfo-box">
            <div class="self-avatar-box">
                <el-image class="self-avatar" fit="fill" :src="avatars[0].url"></el-image>
            </div>
            <div>
                <h2>{{ title }}</h2>
            </div>
            <transition name="el-fade-in-linear">
                <div v-show="showAphorism" class="self-aphorism-box">
                    <blockquote>
                        {{ aphorisms[aphorismIndex].description }}
                    </blockquote>
                    <cite class="self-aphorism-author">
                        — {{ aphorisms[aphorismIndex].author }}
                    </cite>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
export default {
    name: "SelfIntroduction",
    data() {
        return {
            avatars: [
                { id: "avatar_0001", url: require("@/assets/img/avatar/cat1.png") },
            ],
            aphorisms: [
                { "description": "宝剑锋从磨砺出，梅花香自苦寒来。", "author": "《警世贤文・勤奋篇》" },
                { "description": "千磨万击还坚劲，任尔东西南北风。", "author": "郑燮" },
                { "description": "老骥伏枥，志在千里；烈士暮年，壮心不已。", "author": "曹操" },
                { "description": "穷且益坚，不坠青云之志。", "author": "王勃" },
                { "description": "长风破浪会有时，直挂云帆济沧海。", "author": "李白" },
                { "description": "路漫漫其修远兮，吾将上下而求索。", "author": "屈原" },
                { "description": "会当凌绝顶，一览众山小。", "author": "杜甫" },
                { "description": "纸上得来终觉浅，绝知此事要躬行。", "author": "陆游" },
                { "description": "不积跬步，无以至千里；不积小流，无以成江海。", "author": "荀子" },
                { "description": "志不强者智不达。", "author": "墨子" },
                { "description": "盛年不重来，一日难再晨。及时当勉励，岁月不待人。", "author": "陶渊明" },
                { "description": "故天将降大任于是人也，必先苦其心志，劳其筋骨，饿其体肤，空乏其身，行拂乱其所为，所以动心忍性，曾益其所不能。", "author": "孟子" },
                { "description": "博观而约取，厚积而薄发。", "author": "苏轼" },
                { "description": "天生我材必有用，千金散尽还复来。", "author": "李白" },
                { "description": "位卑未敢忘忧国，事定犹须待阖棺。", "author": "陆游" },
                { "description": "非淡泊无以明志，非宁静无以致远。", "author": "诸葛亮" },
                { "description": "莫等闲，白了少年头，空悲切！", "author": "岳飞" },
                { "description": "沉舟侧畔千帆过，病树前头万木春。", "author": "刘禹锡" },
                { "description": "欲穷千里目，更上一层楼。", "author": "王之涣" },
                { "description": "业精于勤，荒于嬉；行成于思，毁于随。", "author": "韩愈" }
            ],
            aphorismIndex: 19,
            showAphorism: true,
        }
    },
    props: {
        title: {
            type: String,
            default: "标题"
        }
    },
    created() {
        setInterval(() => {
            this.aphorismsSwitch();
        }, 15000);
    },
    mounted() {
    },
    computed: {
    },
    methods: {
        aphorismsSwitch() {
            this.$set(this, 'showAphorism', false);
            setTimeout(() => {
                let index = this.aphorismIndex + 1;
                index = index > this.aphorisms.length - 1 ? 0 : index;
                this.$set(this, 'aphorismIndex', index);
                this.$set(this, 'showAphorism', true);
            }, 300);
        },
    }
}
</script>

<style scoped>
.self-introduction-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3000;
    background-color: #FEEBB1;
}

.self-myinfo-box {
    position: relative;
    top: calc(50% - 150px);
    left: calc(50% - 250px);
    width: 500px;
    height: 300px;
    background-color: white;
    border-radius: 15px;
    box-shadow: 2px 2px #E6A23C;
    text-align: center;
    padding: 20px 0;
}

.self-avatar-box {
    position: relative;
    user-select: none;

    .el-image__error {
        border-radius: 50%;
    }
}

.self-avatar {
    width: 100px;
    height: 100px;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.rotate-element {
    animation: spin 3s linear infinite;
}

.self-aphorism-author {
    margin-left: 200px;
}
</style>


